// bootstrap utilities
@import './bootstrap_breakpoints';
@import './bootstrap_colors';

// custom utilities
@import './layout_utilities';


@mixin full-width {
    @include media-breakpoint-up(md) {
        @include margin-left-out(8, 2, 15px);
        @include margin-right-out(8, 2, 15px);
    }
}



@import './comp_form/comp_form';



table.table-responsive {
    display: table;
}


// js-lab
div.js-lab {
    @include full-width;
}

div.js-lab,
div.js-show {

    height: 510px;
    @include media-breakpoint-down(sm) {
        height: 80vh;
    }
}

.aspect-2-1 {
    padding-top: calc((100% + 10px) * .5 + 20px);
    position: relative;
}

.aspect-4-1 {
    padding-top: calc((100% + 10px) * .25 + 20px);
    position: relative;
}

.aspect-2-1 > .js-show,
.aspect-4-1 > .js-show 
{
    position: absolute;
    top: 0;
    height: 100%;
    width: 100%;
}




iframe.js-lab,
iframe.js-show {
    border: none;
    width: 100%;
    height: 100%;
}



