  line-height: 25px;
  font-weight: 200;
  color: #666666; }

.comp-form-copy .slide-info .comments {
  margin-top: 30px;
  margin-bottom: 30px; }

.comp-form-copy .slide-info-bottom a {
  display: block;
  width: max-content;
  margin-bottom: 15px;
  font-size: .9em;
  padding-bottom: 0;
  line-height: 1; }

.comp-form-copy .slide-info-top {
  position: absolute;
  top: 15px;
  right: 0;
  height: 40px;
  left: calc(100%/12*8); }

.comp-form-copy .carousel-control-prev {
  left: 0px; }

.comp-form-copy .carousel-control-next {
  left: 40px; }

.comp-form-copy .slide-info-top polygon {
  fill: #000; }

.comp-form-copy .carousel-control-prev,
.comp-form-copy .carousel-control-next,
.comp-form-copy .carousel-control-prev:hover,
.comp-form-copy .carousel-control-next:hover {
  background: none;
  border-bottom: none;
  width: 50px; }

canvas.mess {
  position: fixed;
  margin: 0;
  padding: 0;
  overflow: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  pointer-events: none;
  transition: opacity .25s;
  /* mix-blend-mode: multiply; */
  /* border: 1px solid red; */ }

canvas.hide {
  opacity: 0;
  transition: opacity 1s; }

.comp-form-copy div.js-lab {
  margin-top: 30px;
  margin-bottom: 30px; }

table.table-responsive {
  display: table; }

@media (min-width: 768px) {
  div.js-lab {
    margin-left: calc( (100% + 15px * 2) / -8 * 2 - 15px);
    margin-right: calc( (100% + 15px * 2) / -8 * 2 - 15px); } }

div.js-lab,
div.js-show {
  height: 510px; }
  @media (max-width: 767px) {
    div.js-lab,
    div.js-show {
      height: 80vh; } }

.aspect-2-1 {
  padding-top: calc((100% + 10px) * .5 + 20px);
  position: relative; }

.aspect-4-1 {
  padding-top: calc((100% + 10px) * .25 + 20px);
  position: relative; }

.aspect-2-1 > .js-show,
.aspect-4-1 > .js-show {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%; }

iframe.js-lab,
iframe.js-show {
  border: none;
  width: 100%;
  height: 100%; }



